@mixin visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@mixin reset-list-style {
  margin: 0;
  padding: 0;

  list-style: none;
}

@mixin line {
  position: relative;
  &::before {
    position: absolute;
    content: "";
    width: 71%;
    height: 2px;
    background-color: $dark-color;
    top: 24px;
    right: $l-padding;
  }
}

